import React, {Component} from 'react';
import { Query }                from 'react-apollo'
import gql                      from 'graphql-tag'
import Link                     from 'next/link'
import { Container, Row, Col, Carousel }  from 'react-bootstrap'

import './SliderMainPage.less';

export const mpslider = gql`
query mpslides{
  mpslides{
    id
    name
    image
    firstHeader
    secondHeader
    button
    url
  }
}
`

class SliderMainPage extends Component{
  render () {
    return (
      <Query query={mpslider}>
      {({loading, error, data: { mpslides }}) => {
        if (error) return <ErrorMessage message='Error loading posts.' />
        if (loading) return <div>Loading</div>
        return (
          <div className="container-fluid sliderHomePageComponent">
            <Carousel>
              {mpslides.map((slide,index)=>{
                return(
                  <Carousel.Item key={index}>
                    <div className="container-fluid">
                      <Row>
                        <div className="col-6 text-align-right">
                          <img className="img-slider" src={"/static/images/sliders/"+slide.image+".png"} />
                        </div>
                        <div className="col-6">
                          <h3>{slide.firstHeader}</h3>
                          <p>{slide.secondHeader}</p>
                        </div>
                      </Row>
                    </div>
                  </Carousel.Item>
                );
              })}
            </Carousel>
          </div>
        )
      }}
      </Query>
    )
  }
}
export default SliderMainPage;
